const string = `
.skin * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  .skin *::before,
  .skin *::after {
    box-sizing: border-box;
  }
  .skin {
    position: relative;
    background: #ffe600;
    min-height: 50vh;
  }
  .nose {
    position: absolute;
    border: 10px solid black;
    border-color: black transparent transparent;
    border-bottom: none;
    width: 0px;
    height: 0px;
    left: 50%;
    top: 145px;
    margin-left: -10px;
    z-index: 10;
  }
  @keyframes wave{
    0%{
      transform:rotate(0deg)
    }
    33%{
      transform:rotate(6deg)
    }
    66%{
      transform:rotate(-6deg)
    }
    100%{
      transform:rotate(0deg)
    }
  }
  .nose:hover{
    animation: wave 600ms infinite linear;
  }
  .yuan {
    position: absolute;
    width: 20px;
    height: 8px;
    margin-left: -10px;
    top: -18px;
    border-radius: 10px 10px 0px 0px;
    background: black;
  }
  .eye {
    position: absolute;
    border: 2px solid black;
    border-radius: 50%;
    width: 64px;
    height: 64px;
    left: 50%;
    top: 100px;
    margin-left: -32px;
    background: #2e2e2e;
  }
  .eye::before {
    content: "";
    display: block;
    border: 3px solid black;
    width: 30px;
    height: 30px;
    background: white;
    border-radius: 50%;
    position: relative;
    left: 4px;
    top: 2px;
  }
  .eye.left {
    transform: translateX(-100px);
  }
  .eye.right {
    transform: translateX(100px);
  }
  .mouth {
    width: 200px;
    height: 200px;
    position: absolute;
    left: 50%;
    top: 170px;
    margin-left: -100px;
  }
  .mouth .up {
    position: relative;
    top: -20px;
    z-index: 1;
  }
  .mouth .up .lip {
    border: 3px solid black;
    height: 30px;
    width: 100px;
    background:#ffe600;
    border-top-color: transparent;
    border-right-color: transparent;
    position: relative;
    position: absolute;
    margin-left: -50px;
    left: 50%;
  }
  .mouth .up .lip.left {
    border-radius: 0 0 0 50px;
    transform: rotate(-15deg) translateX(-53px);
  }
  
  .mouth .up .lip.right {
    border-radius: 0 0 50px 0;
    transform: rotate(15deg) translateX(53px);
  }
  .mouth .up .lip::before {
    content: "";
    display: block;
    width: 7px;
    height: 30px;
    bottom: 0;
    background: #ffe600;
    position: absolute;
  }
  .mouth .up .lip.left::before {
    right: -6px;
  }
  .mouth .up .lip.right::before {
    left: -6px;
  }
  .mouth .down {
    height: 180px;
    width: 100%;
    position: absolute;
    top: 5px;
    overflow: hidden;
  }
  .mouth .down .yuan1{
    border:3px solid black;
    width:150px;
    height:1000px;
    position: absolute;
    left:50%;
    bottom:0;
    margin-left:-75px;
    border-radius:75px/300px;
    background:#9b000a;
    overflow:hidden;
  }
  .mouth .down .yuan1 .yuan2{
    border:1px solid green;
    width:200px;
    height:300px;
    position:absolute;
    bottom:-160px;
    left:50%;
    margin-left:-100px;
    border-radius:100px;
    background:#ff485f;
  }
  .face{
    position:absolute;
    border:3px solid black;
    width:88px;
    height:88px;
    top:200px;
    left:50%;
    margin-left:-44px;
    z-index: 3;
  }
  .face >img{
    left:50%;
    top:50%;
    position:absolute;
  }
  .face.left{
    transform:translateX(-180px);
    background:#ff0000;
    border-radius:50%;
  }
  .face.left >img{
    transform:rotateY(180deg);
    transform-origin:0 0;
  }
  .face.right{
    transform:translateX(180px);
    background:#ff0000;
    border-radius:50%;
  }
`;
let n = 1;
demo.innerText = string.substr(0, n);
demo2.innerHTML = string.substr(0, n);
let time = 50;
const animation = () => {
  n += 1;
  if (n > string.length) {
    window.clearInterval(id);
    return;
  }
  demo.innerText = string.substr(0, n);
  demo2.innerHTML = string.substr(0, n);
  demo.scrollTop = demo.scrollHeight;
};
const play = () => {
  return setInterval(animation, time);
};
const clear = () => {
  window.clearInterval(id); //暂停播放
};
let id = play();
btnPause.onclick = () => {
  clear();
};
btnPlay.onclick = () => {
  id = play();
};
btnSlow.onclick = () => {
  clear();
  time = 200;
  id = play();
};
btnNormal.onclick = () => {
  clear();
  time = 100;
  id = play();
};
btnFast.onclick = () => {
  clear();
  time = 0;
  id = play();
};
